<template>
    <div class="Product">
         <div class="tou">
            <ul>
                <li><span>商品</span></li>
                <li @click="evaluate()"><span>评价</span></li>
            </ul>
        </div>
        <!-- 商品页 -->
        <div class="shoplist" v-if="display">
          <!-- 中间内容 -->
           <div class="middle">
               <!-- 左侧边栏 -->
                <div class="LeftList">
                    <ul>
                        <li v-for="(v,i) in info" :key="i" @click="Clickslide(i)" ref="slide">{{v.name}}</li>
                    </ul>
                </div>
                <!-- 右侧边栏 -->
                <div class="RightList">
                    <div id="box" ref="box">
                        <div class="shopping">
                            <ul v-for="(v,i) in list" :key="i" ref="arr">
                                <li>
                                    <div class="HotSinSa">
                                        <p> 
                                            <span>{{v.name}}</span>
                                            <span>{{v.description}}</span>
                                        </p>
                                    </div>
                                </li>
                                <li v-for="(item,index) in v.foods" :key="index">
                                    <img :src="`http://elm.cangdu.org/img/${item.image_path}`">
                                    <div class="rig">
                                        <p><span>{{item.name}}</span></p>
                                        <p><span>{{item.description}}</span></p>
                                        <p><span>月售{{item.month_sales}}份</span><span>好评率{{item.satisfy_rate}}%</span></p>
                                        <p>
                                            <span>￥{{item.specfoods[0].price}}<span :style="{color:'#676767'}">起</span></span>
                                            <span>
                                                <button @click="jian(item)" v-show="num(item)>0">-</button>
                                                <span v-show="num(item)>0">{{num(item)}}</span>
                                                <button @click="add(item,$event)" :style="{background:'#3190E8',color:'white'}">+</button>
                                            </span>
                                        </p>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
           </div>
            <!-- 评价页 -->
           <div class="evaluateList" v-if="!display">
            <h1>评价</h1>
           </div>
        </div>

        <!-- 底部 -->
        <Footer>
                <div class="left">
                    <div class="wen sc" :style="{background:this.$store.state.gou.length>0?'#3190E8':'#3D3D3F'}" @click="gwc()">
                        <i class="iconfont icon-gouwuche1"></i>
                        <span :style="{background:this.$store.state.gou.length>0?'red':'#3D3D3F'}">{{toNum}}</span>
                    </div>
                    <div>
                        <p>￥{{zong}}</p>
                        <p>配送费￥5</p>
                    </div>
                </div>
                <div class="right">
                   <span @click="$router.push('/home/confirm')">
                       去计算
                   </span>
                </div>
        </Footer>
            <van-action-sheet  v-model="show">
                <ul>
                    <li><span>购物车</span><span @click="empty()"><i class="iconfont icon-qingkong"></i>清空</span></li>
                    <li v-for="(v,i) in $store.state.gou" :key="i" class="tan">
                        <div>{{v.name}}</div>
                        <div>￥{{v.price}}</div>
                        <div>
                            <button @click="subtract(v)"  v-show="num(v)>0">-</button>
                            <span v-show="num(v)>0">{{v.num}}</span>
                            <button @click="plus(v)" :style="{background:'#3190E8',color:'white'}">+</button>
                        </div>
                    </li>
                </ul>
            </van-action-sheet>
    </div>
</template>
<script>
import jquery from "jquery"
import wen from "@/assets/js/theBall.js"
import  "@/assets/css/theBall.css"
export default {
   data() {
      return {
        display:true,
        info:[],
        list:[],
        show:false
      }
   },
   computed: {
       zong(){
            return this.$store.getters.zong
       },
       toNum(){
           return this.$store.getters.toNum
       }
   },
   created() {
       this.axios.get("http://elm.cangdu.org/shopping/v2/menu?restaurant_id=1").then((res)=>{
        //    console.log(res)
           this.info=res.data
       })

        this.axios.get("http://elm.cangdu.org/shopping/v2/menu?restaurant_id=1").then((res)=>{
        //    console.log(res)
           this.list=res.data
       })
   },
   methods: {
    //    左边栏滑动
       Clickslide(i){
        //    console.log(i)
        // 当前li背景默认为空白
           this.$refs.slide.forEach(v => {
               v.style.background=""
           });
        //    让当前带下标得li为白色
           this.$refs.slide[i].style.background='white'
        //   定义初始值为0的高度
           var hei=0;
        //    进入循环让它没个下标赋值到高里    clientHeight当前ref的高
           for(var j=0;j<i;j++){
               hei+=this.$refs.arr[j].clientHeight
           }
        //    jquery动画效果
            jquery('#box').animate({scrollTop:hei},1500)
       },
        //评价
        evaluate(){
            this.$router.push("/home/takeout")
        },
        // 添加商品
        add(item,e){
           wen(e)
                let i=this.$store.state.gou.findIndex(v=>{return v.name==item.name})
                if(i==-1){
                    this.$store.commit("add",item)
                }else{
                    this.$store.commit("jia",i)
                }
        },
        num(item){
            let i=this.$store.state.gou.findIndex(v=>{return v.name==item.name})
            if(i==-1){
                 return 0
            }else{
                return this.$store.state.gou[i].num
            }
        },
        jian(item){
            let i=this.$store.state.gou.findIndex(v=>{return v.name==item.name})
            if(this.$store.state.gou[i].num>1){
                this.$store.commit("jian",i)
            }else{
                this.$store.commit("shan",i)
            }
        },
        plus(v){
            let i=this.$store.state.gou.findIndex(v=>{return v.name==v.name})
                if(i==-1){
                    this.$store.commit("sub",v)
                }else{
                    this.$store.commit("jia",i)
                }
        },
        subtract(v){
            let i=this.$store.state.gou.findIndex(v=>{return v.name==v.name})
            if(this.$store.state.gou[i].num>1){
                this.$store.commit("sub",i)
            }else{
                this.$store.commit("shan",i)
            }
        },
        empty(){
            this.$store.commit("empty")
        },
        gwc(){
            this.show=true
        }
   },
   mounted() {
    //    console.log(this.$route.query.v)
   },
}
</script>

<style lang="scss">
// body{
//     background:#F5F5F5;
// }
*{
    font-size: 0.35rem;
}

.Product{
    width: 100%;
    flex-direction: column;
    display: flex;
    height: 100%;
    .tou{
        width: 100%;
        background: white;
        display: flex;
        // top:1.6rem;
        top:0;
        position: fixed;
        border-bottom: 1px solid rgb(233, 231, 231);
        // box-shadow: 0 0.02rem 0.45rem;
        ul{
            width: 100%;
            display: flex;
            list-style: none;
            li{
                width: 50%;
                height:1rem;  
                text-align: center;
                line-height: 1rem;   
                span:hover{
                    font-size: 0.35rem; 
                    border-bottom: 2px solid #3190E8;
                    color: #3190E8;
                }    
            }
        }
    }
    .shoplist{
        width: 100%;
        height: 100%;
        overflow: hidden;
        flex: 1;
        margin-bottom:1rem;
        .middle{
            width: 100%;
            height: 100%;
            display: flex;
            margin-top:1rem; 
            .LeftList{
                width: 25%;
                height: 100%;
                overflow: auto;
                display: flex;
                flex-direction: column;
                ul{
                    width: 100%;
                    background:#F5F5F5;
                    flex:1;
                    overflow:auto;
                    li{
                        font-size: 0.26rem;
                        padding: 0.6rem 0.35rem;
                        height: 1.5rem;
                        border-bottom: 1px solid #F0F0F0;
                        overflow: hidden;
                        color: #8B8B8B;
                    }
                }
            }
            .RightList{
                width: 75%;
                overflow: auto;
                display: flex;
                flex-direction: column;
                div{
                    flex: 1;overflow: auto;
                    .HotSinSa{
                        width: 100%;
                        p{
                            height: 0.65rem;
                            background: #F5F5F5;
                            >span:nth-of-type(1){
                                font-weight: bold;
                                font-size: 0.3rem;
                                padding-left: 0.35rem;
                                color: #666666;
                            }
                            >span:nth-of-type(2){
                                font-size: 0.25rem;
                                color: #A4A4A4;
                            }
                        }
                    }
                }
                .shopping{
                    width: 100%;
                    ul{
                        width: 100%;
                        list-style: none;
                        background: white;
                        li{
                        border-bottom: 1px solid #FBFBFB;
                        display: flex;
                        line-height: 0.45rem;
                            img{
                                width: 1.2rem;
                                height: 1.05rem;
                                padding-left: 0.2rem;
                                margin-top: 0.15rem;
                            }
                            .rig{
                                padding-left: 0.35rem;
                                >p:nth-child(1){
                                    span{
                                        font-size: 0.3rem;
                                        font-weight: bold;
                                        color: #565656;
                                    }
                                }
                                >p:nth-child(2){
                                    span{
                                        font-size: 0.25rem;
                                        color: gray;
                                    }
                                }
                                >p:nth-child(3){
                                    span:nth-child(1){
                                        font-size: 0.25rem;
                                        color: #4C4C4C;
                                    }
                                    span:nth-child(2){
                                        padding-left: 0.2rem;
                                        font-size: 0.25rem;
                                        color: #4C4C4C;
                                    }
                                }
                                >p:nth-child(4){
                                    display: flex;
                                    justify-content: space-between;
                                    padding-right: 0.1rem;
                                    // position: relative;
                                    span:nth-child(1){
                                        font-size:0.3rem;
                                        color: #FF6600;
                                        font-weight:bold;
                                    }
                                    span:nth-child(2){
                                        button{
                                            width: 0.5rem;
                                            height: 0.5rem;
                                            border-radius: 0.25rem;
                                            border: none;
                                            border: 1px solid #3190E8;
                                            color: #3190E8;
                                            background: white;
                                        }
                                    }
                                }
                               
                            }
                        }
                    }
                }
            }
        } 
    }
    Footer{
        width: 100%;
        position: fixed;
        bottom: 0;
        height: 1rem;
        display: flex;
        z-index: 20000;
        .left{
            width: 7rem;
            display: flex;
            color: white;
            background:#3D3D3F;
            p:nth-child(1){
                margin-top: 0.15rem;                
            }
            p:nth-child(2){
                font-size: 0.2rem;
                padding-left: 0.1rem;
            }
            div:nth-child(1){
                width:1.25rem;
                height:1.25rem;
                border-radius: 50%;
                margin-left: 0.5rem;
                border: 10px solid #444444;
                margin-top: -0.3rem;
                span{
                    position: fixed;
                    margin-top: -0.18rem;
                    margin-left: -0.25rem;
                    border-radius: 45%;
                    width: 0.45rem;
                    height: 0.4rem;
                    font-size: 0.3rem;
                    text-align: center;
                }
            }
            i{
                font-size: 0.65rem;
                margin-left: 0.15rem;
                text-align: center;
                line-height: 1rem;
            }
        }
        .right{
            width: 30%;
            background:#4CD964;
            color: white; 
            line-height:1rem;
            text-align: center;
            font-size: 0.3rem;
            font-weight: bold;
        }   
    }
    .van-action-sheet{
        position: relative;
        bottom: 1rem;
        ul{
            width: 100%;
            li:nth-child(1){
                display: flex;
                justify-content: space-between;
                background: #ECEFF1;
                height: 0.8rem;
                span:nth-child(1){
                    font-size: 0.45rem;
                    margin-top: 0.10rem;
                }
                span:nth-child(2){
                    margin-top: 0.10rem;
                    i{
                        font-size: 0.3rem;
                        color: #BBBBBB;
                        padding-right:0.3rem;
                    }
                    span{
                        padding-right: 0.2rem;
                    }
                }
            }
            .tan{
                display: flex;
                justify-content: space-between;
                height: 0.8rem;
                div:nth-child(1){
                    margin-top: 0.10rem;
                    color: #676767;                    
                }
                div:nth-child(2){
                    margin-top: 0.10rem;
                    color: #FF6600;    
                    position: fixed;
                    margin-left:4rem;
                }
                div:nth-child(3){
                    margin-top: 0.10rem; 
                    button{
                        width: 0.5rem;
                        height: 0.5rem;
                        border-radius: 0.25rem;
                        border: none;
                        border: 1px solid #3190E8;
                        color: #3190E8;
                        background: white;
                    }                
                }
            }
        }
    }
}

</style>


